.body {
    flex: 0.8;
    background: linear-gradient(rgb(91, 87, 115), rgb(0,0,0,1));
    color: white;
    height: 100vh;
    padding: 30px;
    overflow-y: overlay;
}

.body::-webkit-scrollbar {
    display: none;
}

.body__info {
    display: flex;
    align-items: flex-end;
    padding: 10px;
}

.body__info > img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0, 4px, 60px rgba(0,0,0,0.5);
}
.body__infoText > p {
    font-size: 14px;
}

.body__icons {
    display: flex;
    align-items: center;
}
.body__icons > .MuiSvgIcon-root {
    margin-right: 20px;
    
}
.body__shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.body__shuffle:hover {
    transition: transform 0.1s ease-in-out;
    transform: scale(1.08) !important;
}

.body__songs {
    margin: 20px -30px;
}